
export class AppareilService {

  appareils = [
    {
      id:1,
      name:"Machine  laver",
      status: "teint"
    },
    {
      id:2,
      name:"Frigo",
      status:"allum"
    },
    {
      id:3,
      name:"Ordinateur",
      status:"teint"
    }
  ];

  switchOnAll() {
    for(let appareil of this.appareils) {
      appareil.status = "allum";
    }
  }

  switchOffAll() {
    for(let appareil of this.appareils) {
      appareil.status = "teint";
    }
  }

  switchOnOne(i: number) {
    this.appareils[i].status = 'allum';
  }

  switchOffOne(i: number) {
    this.appareils[i].status = 'teint';
  }

  getAppareilById(id: number) {
    const appareil = this.appareils.find(
      (s) => {
        return s.id === id;
      }
    );
    return appareil;
  }

}
